.impact-bento {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-template-areas:
    "g1 g3"
    "g2 g3";
}

.impact-bg1 {
    &::before {
        display: block;
        position: relative;
        content: '';
        left: -20%;
        width: 140%;
        height: 100%;
        transform: skew(10deg);
        background-image: url('../images/SADA1.jpg');
        background-size: cover;
        background-position: center;
    } 
}

